.card-default {
  background-color: var(--neutral-background-2-default);
  box-shadow: var(--shadow-sm);
}

.card-elevated {
  background-color: var(--neutral-background-2-default);
  box-shadow: var(--shadow-md);
}

.card-outlined {
  background-color: var(--neutral-background-1-default);
  border: 1px solid var(--neutral-stroke-1-default);
}

/* 카드 요소 스타일 */
.card-media {
  width: 100%;
  object-fit: cover;
}

.card-content {
  display: flex;
  flex-direction: column;
  padding: var(--spacing-md);
}

.card-title {
  font-family: var(--typography-fontFamily-base);
  font-size: var(--typography-fontSize-xl);
