  color: var(--neutral-foreground-2-default);
  margin-bottom: var(--spacing-sm);
}

.card-body {
  font-family: var(--typography-fontFamily-base);
  font-size: var(--typography-fontSize-md);
  font-weight: var(--typography-fontWeight-regular);
  color: var(--neutral-foreground-1-default);
}

.card-actions {
  display: flex;
  justify-content: flex-end;
  padding: var(--spacing-sm) var(--spacing-md);
  gap: var(--spacing-sm);
  border-top: 1px solid var(--neutral-stroke-2-default);
